      background-size: cover;
      background-position: 50% 50%;
      background-repeat: no-repeat; }
      .list .list-item .img.play-icon:after {
        content: '';
        width: 80px;
        height: 80px;
        background-image: url("../images/owl.video.play.png");
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        position: absolute;
        transition: all 0.3s; }
    .list .list-item .info {
      margin-top: 7px; }
      .list .list-item .info .info-title {
        transition: all 0.3s; }
      .list .list-item .info .info-txt {
        color: #abb7c4;
        line-height: 1.7em; }
  .list.man-list .list-item {
