    .list.man-list .list-item.director {
      width: 100%;
      flex-direction: row; }
      .list.man-list .list-item.director .img {
        width: 50%;
        position: relative;
        flex-shrink: 0; }
        .list.man-list .list-item.director .img:before {
          display: block;
          content: "";
          width: 100%;
          padding-top: 45%; }
        .list.man-list .list-item.director .img > * {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0; }
      .list.man-list .list-item.director .info {
        padding-left: 50px; }
        .list.man-list .list-item.director .info .info-title {
          font-size: 24px; }
        .list.man-list .list-item.director .info .team {
          line-height: 1.7em;
          color: #999;
          font-size: 15px; }
  .list.film-list .list-item {
