    cursor: pointer; }
    .list.film-list .list-item .img {
      opacity: .8;
      transition: all 0.3s;
      position: relative;
      transition: all 0.3s; }
      .list.film-list .list-item .img:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: 56.25%; }
      .list.film-list .list-item .img > * {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0; }
    .list.film-list .list-item .info {
      background-color: #071829;
      margin-top: -5px;
      padding: 15px 35px; }
    .list.film-list .list-item:hover .img {
      opacity: 1;
      transform: scale(1.05); }
    .list.film-list .list-item:hover .info .info-title {
      color: #4280bf; }
  .list.film-list.film-list-2 .list-item {
    width: 100%;
    flex-direction: row;
    margin-bottom: 40px; }
    .list.film-list.film-list-2 .list-item .img {
      width: 50%; }
      .list.film-list.film-list-2 .list-item .img .note {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.7);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column; }
        .list.film-list.film-list-2 .list-item .img .note .date {
          letter-spacing: 0.3em;
          font-size: 28px; }
        .list.film-list.film-list-2 .list-item .img .note .note-txt {
          text-transform: uppercase;
          margin-top: 15px;
          letter-spacing: 0.15em; }
    .list.film-list.film-list-2 .list-item .info {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-left: 70px;
      background-color: transparent; }
      .list.film-list.film-list-2 .list-item .info .info-title {
        font-size: 24px;
