    .list.pro-list .list-item .img {
      position: relative;
      transition: all 0.3s; }
      .list.pro-list .list-item .img:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: 107.5%; }
      .list.pro-list .list-item .img > * {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0; }
    .list.pro-list .list-item:hover .info .info-title {
      color: #4280bf; }
    .list.pro-list .list-item:hover .img {
